@charset "UTF-8";
*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body.no-scroll {
  overflow: hidden; }

body::-webkit-scrollbar {
  width: 6px;
  background-color: #28303F; }

body::-webkit-scrollbar-thumb {
  background-color: #ff0078; }

.App {
  height: 100%;
  overflow-x: hidden; }

.App::-webkit-scrollbar {
  width: 6px;
  background-color: #28303F; }

.App::-webkit-scrollbar-thumb {
  background-color: #ff0078; }

#root {
  height: 100%; }

aside {
  width: 400px;
  height: 100%;
  background-color: #F7F7F7;
  background-repeat: no-repeat !important;
  float: left;
  position: fixed;
  padding: 0 40px;
  z-index: 10;
  transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1); }
  @media (max-width: 992px) {
    aside {
      transform: translateX(-400px);
      width: 400px; } }
  aside.is-visible {
    transform: translateX(0); }
  aside .aside--close-button {
    position: absolute;
    top: 36px;
    right: 0;
    transform: translateX(50%) scale(0);
    background-image: linear-gradient(33deg, #ff0078 0%, #ff0049 100%);
    border: 3px solid #F7F7F7;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    cursor: pointer;
    display: none;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55); }
    @media (max-width: 992px) {
      aside .aside--close-button {
        display: block; } }
    aside .aside--close-button.is-visible {
      transform: translateX(50%) scale(1); }
    aside .aside--close-button .aside--close-button--img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform-origin: center center;
      transform: translate(-50%, -50%) rotate(45deg);
      width: 33px; }
  aside .aside-footer-list {
    position: absolute;
    bottom: 35px;
    width: 100%;
    z-index: 10;
    padding: 0; }
    aside .aside-footer-list .aside-footer-list-item {
      list-style: none;
      padding-inline-start: 0;
      font-family: 'Montserrat';
      font-size: 14px;
      color: #28303F;
      opacity: .32;
      padding: 7px 0; }
  aside .aside-footer-bg {
    position: absolute;
    width: 100%;
    height: auto;
    bottom: 0;
    left: 0; }

main {
  width: calc(100% - 400px);
  min-height: 100%;
  background: #28303F;
  float: right;
  transform: translateX(0);
  transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
  position: relative; }
  @media (max-width: 992px) {
    main {
      width: 100%; } }
  main .main-header {
    width: 100%;
    height: 125px;
    position: relative;
    opacity: 0;
    transition: all 0.5s; }
    @media (max-width: 500px) {
      main .main-header {
        height: 150px; } }
    main .main-header.is-visible {
      opacity: 1;
      transition: all 0.5s; }

.grid {
  overflow: auto;
  transform-origin: center top;
  transform: translateY(50px);
  opacity: 0;
  transition: all 0.5s;
  padding: 17.5px; }
  .grid.is-visible {
    transform: translateY(0);
    opacity: 1;
    transition: all 0.5s; }

.no-results {
  padding: 17.5px;
  text-align: center; }
  .no-results .no-results--inner {
    padding: 17.5px;
    background: #3D4552;
    margin: auto; }
    .no-results .no-results--inner img {
      width: 100px; }
    .no-results .no-results--inner .no-results--title {
      color: #F7F7F7;
      font-size: 22px;
      font-weight: 500;
      font-family: 'Montserrat'; }
    .no-results .no-results--inner .no-results--button {
      background-image: linear-gradient(33deg, #ff0078 0%, #ff0049 100%);
      color: #F7F7F7;
      padding: 15px 17px;
      font-size: 16px; }

.content {
  overflow: auto;
  min-width: 100%;
  min-height: 100%;
  position: relative; }
  .content::-webkit-scrollbar {
    width: 6px;
    background-color: #28303F; }
  .content::-webkit-scrollbar-thumb {
    background-color: #ff0078; }
  .content .content-background {
    position: absolute;
    width: 100%;
    min-height: 100vh;
    background-size: 100% auto !important;
    background-position: center -200px !important;
    background-repeat: no-repeat !important;
    /* Rectangle: */ }
    @media (max-width: 1200px) {
      .content .content-background {
        background-position: center -150px !important; } }
    @media (max-width: 500px) {
      .content .content-background {
        background-position: center -50px !important; } }
  .content .content-gradient {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Rectangle: */
    background-image: linear-gradient(0deg, #28303F 0%, rgba(40, 48, 63, 0.75) 100%); }
  .content .content-details {
    position: absolute;
    width: 100%;
    background: transparent; }
    .content .content-details .content-details-header {
      position: relative;
      width: 100%;
      height: 170px; }
      .content .content-details .content-details-header .content-close {
        position: absolute;
        top: 40px;
        right: 30px;
        border: 2px solid #F7F7F7;
        color: #F7F7F7;
        padding: 15px 17px;
        font-size: 16px; }
    .content .content-details .content-details-body {
      width: 100%;
      padding: 0 60px 120px 60px;
      overflow: auto; }
      @media (max-width: 768px) {
        .content .content-details .content-details-body {
          padding: 0 30px 60px 30px; } }
      .content .content-details .content-details-body .content-details-body-col1 {
        padding: 0;
        width: 215px;
        height: auto;
        max-height: initial;
        float: left; }
        @media (max-width: 1200px) {
          .content .content-details .content-details-body .content-details-body-col1 {
            float: none;
            margin: auto; } }
        .content .content-details .content-details-body .content-details-body-col1 img {
          padding: 0;
          width: 100%;
          max-height: initial; }
      .content .content-details .content-details-body .content-details-body-col2 {
        width: 635px;
        padding-left: 30px;
        float: left; }
        @media (max-width: 1600px) {
          .content .content-details .content-details-body .content-details-body-col2 {
            width: 450px; } }
        @media (max-width: 1200px) {
          .content .content-details .content-details-body .content-details-body-col2 {
            width: 100%;
            padding-top: 60px; } }
        @media (max-width: 500px) {
          .content .content-details .content-details-body .content-details-body-col2 {
            padding-left: 0; } }
  .content .body-h1 {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 44px;
    margin: 0; }
  .content .body-highlighted {
    font-family: 'Montserrat';
    font-weight: 600;
    color: #F7F7F7;
    display: block;
    margin-bottom: 30px; }
  .content .body-p {
    font-family: 'Montserrat';
    font-weight: 600;
    color: #A2ACBB;
    line-height: 23px;
    margin-bottom: 30px; }
  .content ul {
    color: #A2ACBB;
    font-family: 'Montserrat';
    font-weight: 500;
    list-style-type: none;
    padding-inline-start: 0;
    padding: 0;
    margin: 0; }
    .content ul li {
      padding: 10px 0; }
      .content ul li span {
        display: block;
        color: #F7F7F7;
        width: 140px;
        float: left; }
      .content ul li a {
        /* TODO base pour César */
        color: #fff;
        text-decoration: none; }
